body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  word-break: break-all;
}

.screen {
  height: 30rem;
  background-color: #282923;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 350px) {
  .screen {
    height: 15rem;
  }
}
.screen .title {
  color: #fff;
  font-size: 4rem;
  font-weight: bold;
}
@media screen and (max-width: 1000px) {
  .screen .title {
    font-size: 2.5rem!important;
  }
}
@media screen and (max-width: 350px) {
  .screen .title {
    font-size: 2rem!important;
  }
}
.screen .intro {
  color: #f92372;
  font-size: 2rem;
}
@media screen and (max-width: 1000px) {
  .screen .intro {
    font-size: 1.5rem!important;
  }
}
@media screen and (max-width: 350px) {
  .screen .intro {
    font-size: 1rem!important;
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
  width: 100%;
}
.main .center {
  margin: 0 auto;
}
.main .subtitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.main .screen .title {
  color: #fff;
  font-size: 6rem;
  font-weight: bold;
}
.main .screen .intro {
  color: #f92372;
  font-size: 3rem;
}
.main .desc {
  padding: 1rem;
  background-color: #f6f6f6;
}
.main .episodes {
  padding: 1rem;
}
.main .episodes .episode {
  padding: 1rem 0;
}
.main .episodes .episode .title {
  font-weight: bold;
  text-decoration: none;
  box-shadow: 0 1px rgba(51,122,183,0.2);
  color: #7eaf22;
}
.main .episodes .episode .title:hover {
  color: #a3e22c;
}
.main .episodes .episode .intro {
  color: #74705b;
  font-size: 0.8rem;
  margin-bottom: 10px;
}

.nav-bar {
  width: 100%;
}

.btns {
  text-align: center;
  padding: 1rem;
}
.btns a button {
  width: 100%;
}
.btns a + a {
  margin-left: 10px;
}
.btns .faq-link {
  float: left;
  margin-top: 10px;
  font-size: 14px;
}
@media (max-width: 600px) {
  .btns a button {
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
  }
}

.episode {
  width: 100%;
}
.episode .center {
  margin: 0 auto;
}
.episode .subtitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.episode .audio {
  background-color: #000;
}
.episode .desc {
  padding: 1rem;
  background-color: #f6f6f6;
}

.mejs__speed-button,
.mejs-speed-button {
    position: relative;
}

.mejs__speed-button > button,
.mejs-speed-button > button {
    background: transparent;
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    line-height: normal;
    margin: 11px 0 0;
    width: 36px;
}

.mejs__speed-selector,
.mejs-speed-selector {
    background: rgba(50, 50, 50, 0.7);
    border: solid 1px transparent;
    border-radius: 0;
    left: -10px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    top: -180px;
    visibility: hidden;
    width: 60px;
}

.mejs__speed-selector,
.mejs-speed-selector {
    visibility: visible;
}

.mejs__speed-selector-list,
.mejs-speed-selector-list  {
    display: block;
    list-style-type: none !important;
    margin: 0;
    overflow: hidden;
    padding: 0;
}

.mejs__speed-selector-list-item,
.mejs-speed-selector-list-item {
    color: #fff;
    display: block;
    list-style-type: none !important;
    margin: 0 0 6px;
    overflow: hidden;
    padding: 0 10px;
}

.mejs__speed-selector-list-item:hover,
.mejs-speed-selector-list-item:hover {
    background-color: rgb(200, 200, 200) !important;
    background-color: rgba(255, 255, 255, 0.4) !important;
}

.mejs__speed-selector-input,
.mejs-speed-selector-input {
    clear: both;
    float: left;
    left: -1000px;
    margin: 3px 3px 0 5px;
    position: absolute;
}

.mejs__speed-selector-label,
.mejs-speed-selector-label {
    color: white;
    cursor: pointer;
    float: left;
    font-size: 11px;
    line-height: 15px;
    margin-left: 5px;
    padding: 4px 0 0;
    width: 60px;
}

.mejs__speed-selected,
.mejs-speed-selected {
    color: rgba(33, 248, 248, 1);
}

.mejs__speed-selector,
.mejs-speed-selector {
    visibility: hidden;
}
.mejs__speed-button:hover .mejs__speed-selector,
.mejs-speed-button:hover .mejs-speed-selector {
    visibility: visible;
}

